import React from 'react';
import axios from 'axios';
import { API_ROOT_URL } from '../api-config';
import { PageHeader, Layout, Typography, Table, } from 'antd';

const { Text } = Typography;
const { Content } = Layout;
export default class Category extends React.Component {
    constructor() {
        super();
        this.state = {
            dataSource: [],
            loading: true,
            columns: [
                {
                    title: 'English',
                    dataIndex: 'engPhraseName',
                    key: 'engPhraseName',
                    render: (text, record) => (
                        <h4>{record.engPhraseName}</h4>
                    )
                },
                {
                    title: 'Telugu',
                    dataIndex: 'nonEngPhrases',
                    key: 'nonEngPhrases',
                    render: (text, record) => (
                        <>{text.map((item, key) => (
                            <>
                                <h4>{item.transliteration}</h4>
                                <Text type="secondary">{item.nonEngPhraseName}</Text>
                            </>
                        ))}</>
                    )
                },
            ]
        }
    }

    componentDidMount() {
        const config = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        };
        axios.get(`${API_ROOT_URL}/topics/${this.props.match.params.name}/phrases`, config)
            .then(res => {
                this.setState({ dataSource: res.data, loading: false });
            })
    }

    render() {
        const { dataSource, loading, columns } = this.state;
        return (
            <Content>
                <PageHeader
                    className="site-page-header"
                    onBack={() => this.props.history.goBack()}
                    title={this.props.match.params.name}
                />
                {!loading && <Table dataSource={dataSource} columns={columns} rowKey="engPhraseName" size="small" />}
            </Content>
        )
    }
}